.Card{
    flex: 1;
    margin: 20px;
    border: 3px solid #000;
    border-radius: 10px;
    overflow: hidden;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    min-width: 250px;
    min-height: 250px;
}

.Card .Content{
    flex: 1;
    color: #000;
    padding: 15px 0px;
}
.Card .Footer{
    background-color: #000000;
    color: rgb(29, 34, 77);
    padding: 5px 0px;
}
